import React from 'react';
import PropTypes from 'prop-types';
import Tag from '../tag';
import ExperienceElement from './experience-element';

export default class Experience extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      areExpanded: false,
      isExpanded: Array(props.data.length).fill().map((el, index) => index < 3),
      data: props.data,
    };
    this._toggleAllExperiences = this.toggleAllExperiences.bind(this);
  }

  toggleAllExperiences() {
    let { areExpanded } = this.state;
    const { isExpanded: oldIsExpanded } = this.state;
    areExpanded = !areExpanded;
    const isExpanded = oldIsExpanded.map(() => areExpanded);
    this.setState({ areExpanded, isExpanded });
  }

  render() {
    const { data = [], isExpanded = [], areExpanded } = this.state;
    const { highlights } = this.props;
    return (
      <section aria-labelledby="experience-heading">
        <h3 id="experience-heading">Professional Experience</h3>
        {highlights.length === 0 &&
          <Tag className="experience__toggle-all no-print" toggle={this._toggleAllExperiences}>
            {areExpanded ? 'Collapse all' : 'Expand all'}
          </Tag>
        }
        {data.map((e, index) => (
          <ExperienceElement
            key={`${e.id}`}
            experience={e}
            shouldPrint={e.print}
            isExpanded={isExpanded[index]}
            highlights={highlights}
          />
        ))}
        
        <div className='end-disclaimer'>
          Note: For clarity, not all jobs may be listed here.
          For more details about previous jobs, visit https://pfernandom.github.io 
          or my LinkedIn profile
        </div>

      </section>
    );
  }
}
Experience.propTypes = {
  data: PropTypes.arrayOf().isRequired,
  highlights: PropTypes.arrayOf().isRequired,
};
