import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import ContactCard from './contact-card';
import { ExperienceList } from './experience';
import Tag from './tag';
import Tooltip from './tooltip';
import './home.scss';

import { toggleHighlightAction, setHighlightsAction, setRoleAction } from '../state/app';

const Instruction = ({ text }) => (
  <Tooltip className="instructions pulse" hoverText={text}>
    &#8984;
  </Tooltip>
);
Instruction.propTypes = {
  text: PropTypes.string.isRequired,
};

const Skill = ({ tag, isSelected, onToggleSkill }) => {
  function updateSelectedTag() {
    onToggleSkill(tag);
  }

  return <Tag key={`${tag}`} value={tag} isSelected={isSelected} toggle={updateSelectedTag} />;
};
Skill.propTypes = {
  tag: PropTypes.string.isRequired,
  isSelected: PropTypes.bool.isRequired,
  onToggleSkill: PropTypes.func,
};
Skill.defaultProps = {
  onToggleSkill() {},
};

class Home extends React.Component {
  constructor(props) {
    super(props);
    const { roles, setHighlights, role } = props;

    setHighlights(roles.filter(curRole => curRole.title === role).flatMap(r => r.tags));

    this._onToggleSkill = this.onToggleSkill.bind(this);
  }

  onToggleSkill(tag) {
    const { toggleHighlight } = this.props;
    toggleHighlight(tag);
  }

  updateSelectedRoles(selectedRole) {
    const { role: stateRole, roles, setHighlights, setRole } = this.props;
    const role = selectedRole === stateRole ? '' : selectedRole;
    setRole(role);
    const highlights = roles.filter(curRole => curRole.title === role).flatMap(r => r.tags) || [];
    setHighlights(highlights);
  }

  isRoleSelected(selectedRole) {
    const { role } = this.props;
    return selectedRole === role;
  }

  isTagSelected(tag) {
    const { highlights = [] } = this.props;
    return highlights.includes(tag);
  }

  clearTags() {
    this.updateSelectedRoles('');
    const { setHighlights } = this.props;
    setHighlights([]);
  }

  render() {
    const {
      identification: { role, name, summary, contact = [], highlights: idHighlights },
      roles = [],
      experience,
      highlights,
      role: stateRole,
    } = this.props;
    const tags = Array.from(new Set(roles.flatMap(cur => cur.tags)));
    const hasHighlights = highlights.length > 0;
    const skillsClassNames = ['skills-tags', hasHighlights && 'no-print'];
    return (
      <div className="summary" id="top">
        <ContactCard
          role={stateRole || role}
          name={name}
          summary={summary}
          contact={contact}
          highlights={idHighlights}
        />

        <section aria-labelledby="roles-heading" className="no-print">
          <h3 id="roles-heading">Roles</h3>
          <Instruction text="Select a role to filter the experience related to it" />
          <div className="roles tags-collection">
            {roles.length <= 0 ? (
              <span>Loading roles...</span>
            ) : (
              roles.map(curRole => (
                <Tag
                  key={curRole.title}
                  value={curRole.title}
                  isSelected={this.isRoleSelected(curRole.title)}
                  // eslint-disable-next-line react/jsx-no-bind
                  toggle={this.updateSelectedRoles.bind(this, curRole.title)}
                />
              ))
            )}
          </div>
        </section>
        <section aria-labelledby="skills-heading" className={skillsClassNames.join(' ')}>
          <h3 id="skills-heading">Skills</h3>
          <Instruction text="Select one or more skills to filter the experience by them" />
          {highlights.length > 0 ? (
            <button className="no-print clear" onClick={this.clearTags.bind(this)}>
              Clear skills
            </button>
          ) : null}
          <div className="tags-collection no-print">
            {tags.length <= 0 ? (
              <span>Loading skills...</span>
            ) : (
              tags.map(tag => (
                <Skill
                  key={tag}
                  tag={tag}
                  isSelected={this.isTagSelected(tag)}
                  onToggleSkill={this._onToggleSkill}
                />
              ))
            )}
          </div>
          <div className="only-print tags-collection--print" aria-hidden="true">
            {tags.filter(tag => this.isTagSelected(tag)).join(', ')}
          </div>
          <hr />
        </section>
        <ExperienceList data={experience} highlights={highlights} />
      </div>
    );
  }
}

Home.propTypes = {
  role: PropTypes.string.isRequired,
  toggleHighlight: PropTypes.func.isRequired,
  setHighlights: PropTypes.func.isRequired,
  setRole: PropTypes.func.isRequired,
  highlights: PropTypes.arrayOf(PropTypes.string).isRequired,
  roles: PropTypes.arrayOf(PropTypes.string).isRequired,
  identification: PropTypes.objectOf({}).isRequired,
  experience: PropTypes.objectOf({}).isRequired,
};

export default connect(
  ({ app: { highlights, role } }) => ({ highlights, role }),
  {
    toggleHighlight: toggleHighlightAction,
    setHighlights: setHighlightsAction,
    setRole: setRoleAction,
  },
)(Home);
